.contentBlock {
    padding: 20px;
    box-shadow: 0px 3px 8px rgb(74, 231, 158);
    border-radius: 5px;
    margin: 10px;
    width: 80%;
    background: #fff;
}
textarea {
    width: 250px;
    height: 20px;
    /* border-color: rgb(197, 197, 197); */
    border-color: rgb(95, 95, 95); 
    border-radius: 3px;
    color: rgb(190, 190, 190);
    margin-bottom: 15px;
}
.wrapperLoginBox {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
}
form input {
    margin-bottom: 15px;
}
.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: rgb(255, 0, 0);
    font-weight: bold;
    width: 125px;
}